.image-wrapper {
  img {
    width: 100%;
    height: auto;
    border-radius: 1rem;
    overflow: hidden;
  }

  .caption {
    text-align: center;
  }
}

.works-image {
  .caption {
    background: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 5px 10px 2px rgba(0,0,0,0.2);
    backdrop-filter: blur(5px);
    border-radius: 0.5rem;;
  }
}